:root {
  --md-sys-color-primary: #2196f3;
  --md-sys-color-secondary: #ff4081;
  --background: #f0f0f0;
  --font: #000;
}

body {
  background: var(--background);
  color: var(--font);
  margin: 0;
  padding: 0 24px;
  font-family: Helvetica, Arial, sans-serif;
}

header, main {
  background-color: orange;
  max-width: 1140px;
  margin: 24px auto 0;
}

header {
  background-color: #e0e0e0;
  border-radius: 32px;
  height: 64px;
  position: relative;
  overflow: hidden;
}

button {
  background-color: var(--md-sys-color-primary);
  color: #fff;
  cursor: pointer;
  border: none;
  padding: 0 12px;
}

#submit {
  position: inherit;
  float: right;
  z-index: 2;
  border-radius: 16px;
  height: 32px;
  margin: 16px;
}

#github-link {
  position: inherit;
  z-index: 2;
  float: left;
  background-image: url("github-brands-solid-full.a1c4b5e6.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  margin: 16px;
  display: block;
}

#progress {
  z-index: 1;
  width: 0%;
  height: inherit;
  background-color: var(--md-sys-color-primary);
  opacity: .25;
  transition: width .1s;
  position: absolute;
  top: 0;
  left: 0;
}
